import { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { QuoteToken } from 'config/constants/types'
import { getTokenAddress } from 'utils/addressHelpers'
import { provider } from 'web3-core'
import { getTokenBalance } from 'utils/erc20'
import useRefresh from './useRefresh'
import { useToken } from './useContract'

const useTokenBalance = (token: QuoteToken) => {
  const [balance, setBalance] = useState(new BigNumber(0))
  const { account, ethereum }: { account: string; ethereum: provider } = useWallet()
  const { fastRefresh } = useRefresh()
  const tokenAddress = getTokenAddress(token)

  useEffect(() => {
    const fetchBalance = async () => {
      const res = await getTokenBalance(ethereum, tokenAddress, account)
      setBalance(new BigNumber(res))
    }

    if (account && ethereum) {
      fetchBalance()
    }
  }, [account, ethereum, tokenAddress, fastRefresh])

  return balance
}

export const useTokenBalanceInAddress = (tokenAddress: string, address: string) => {
  const [balance, setBalance] = useState(new BigNumber(0))
  const { ethereum }: { ethereum: provider } = useWallet()
  const { fastRefresh } = useRefresh()

  useEffect(() => {
    const fetchBalance = async () => {
      const res = await getTokenBalance(ethereum, tokenAddress, address)
      setBalance(new BigNumber(res))
    }

    if (address && ethereum) {
      fetchBalance()
    }
  }, [address, ethereum, tokenAddress, fastRefresh])

  return balance
}

export const useTotalSupply = (token: QuoteToken) => {
  const { slowRefresh } = useRefresh()
  const [totalSupply, setTotalSupply] = useState<BigNumber>()
  const contract = useToken(token)

  useEffect(() => {
    async function fetchTotalSupply() {
      const supply = await contract.methods.totalSupply().call()
      setTotalSupply(new BigNumber(supply))
    }

    fetchTotalSupply()
  }, [slowRefresh, contract])

  return totalSupply
}

export default useTokenBalance
