import { useCallback, useState, useEffect } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { Contract } from 'web3-eth-contract'
import { useDispatch } from 'react-redux'
import { FarmType, QuoteToken } from 'config/constants/types'
import { approve } from 'utils/callHelpers'
import BigNumber from 'bignumber.js'
import { getAllowance } from 'utils/erc20'
import { fetchFarmUserDataAsync } from '../state/farms'
import { useBoardroom, useMasterchef, useToken, useTreasury } from './useContract'
import useBlock from './useBlock'

const useApprove = (type: FarmType, lpContract: Contract) => {
  const dispatch = useDispatch()
  const { account }: { account: string } = useWallet()
  const masterChefContract = useMasterchef(type)

  const handleApprove = useCallback(async () => {
    try {
      const tx = await approve(lpContract, masterChefContract, account)
      dispatch(fetchFarmUserDataAsync(account))
      return tx
    } catch (e) {
      return false
    }
  }, [account, dispatch, lpContract, masterChefContract])

  return { onApprove: handleApprove }
}

export const useAllowance = (lpContract: Contract, contract: Contract) => {
  const [isApproved, setIsApproved] = useState(false)
  const { account }: { account: string } = useWallet()
  const block = useBlock()

  useEffect(() => {
    if (isApproved) return
    getAllowance(lpContract, contract, account).then((x) => {
      if (!account) return
      const allowance = new BigNumber(x)
      setIsApproved(allowance && allowance.isGreaterThan(0))
    })
  }, [account, contract, lpContract, block, isApproved])

  return isApproved
}

export const useApproveBoardroom = () => {
  const { account }: { account: string } = useWallet()
  const contract = useBoardroom()
  const share = useToken(QuoteToken.SSO)
  const isApproved = useAllowance(share, contract)

  const handleApprove = useCallback(async () => {
    try {
      const tx = await approve(share, contract, account)
      return tx
    } catch (e) {
      return false
    }
  }, [account, share, contract])

  return { onApprove: handleApprove, isApproved }
}

export const useApproveTreasury = (token: QuoteToken) => {
  const { account }: { account: string } = useWallet()
  const contract = useTreasury()
  const tokenAddress = useToken(token)
  const isApproved = useAllowance(tokenAddress, contract)

  const handleApprove = useCallback(async () => {
    try {
      const tx = await approve(tokenAddress, contract, account)
      return tx
    } catch (e) {
      return false
    }
  }, [account, tokenAddress, contract])

  return { onApprove: handleApprove, isApproved }
}

export default useApprove
