import { useCallback, useEffect, useState } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { TreasuryAllocationTime } from 'utils'
import { getTreasury } from 'utils/addressHelpers'
import multicall from 'utils/multicall'
import treasury from 'config/abi/treasury.json'
import BigNumber from 'bignumber.js'
import { useBoardroom, useTreasury } from './useContract'
import useRefresh from './useRefresh'

const useExitBoardroom = () => {
  const { account } = useWallet()
  const contract = useBoardroom()

  const handleExit = useCallback(async () => {
    const txHash = await contract.methods.exit().send({ from: account })
    return txHash
  }, [account, contract])

  return { onRedeem: handleExit }
}

const oneUnit = new BigNumber(10).pow(18)

export const useCashPriceInEstimatedTWAP = () => {
  const [stat, setStat] = useState(new BigNumber(10).div(oneUnit))
  const contract = useTreasury()
  const { slowRefresh } = useRefresh()

  useEffect(() => {
    contract.methods.getDollarPrice().call().then(x => setStat(new BigNumber(x).div(oneUnit)))
    
  }, [contract, slowRefresh])

  return stat
}

export const useTreasuryAllocationTimes = () => {
  const [time, setTime] = useState<TreasuryAllocationTime>({
    prevAllocation: new Date(),
    nextAllocation: new Date(),
    nextEpochLength: 0,
  })
  const contract = getTreasury()

  useEffect(() => {
    const fetchAllocations = async () => {
      const calls = [
        {
          address: contract,
          name: 'nextEpochPoint',
          params: [],
        },
        {
          address: contract,
          name: 'lastEpochTime',
          params: [],
        },
        {
          address: contract,
          name: 'nextEpochLength',
          params: [],
        },
      ]

      const [nextEpochPoint, lastEpochTimestamp, nextEpochLength] = await multicall((treasury as unknown) as any, calls)
      setTime({
        nextAllocation: new Date(new BigNumber(nextEpochPoint).multipliedBy(1000).toNumber()),
        prevAllocation: new Date(new BigNumber(lastEpochTimestamp).multipliedBy(1000).toNumber()),
        nextEpochLength: new BigNumber(nextEpochLength).toNumber(),
      })
    }

    if (contract) fetchAllocations()
  }, [contract])
  return time
}

export const useBondStats = () => {
  const [data, setData] = useState([new BigNumber(0), new BigNumber(0)])
  const contract = getTreasury()

  useEffect(() => {
    const fetchAllocations = async () => {
      const calls = [
        {
          address: contract,
          name: 'getBurnableDollarLeft',
          params: [],
        },
        {
          address: contract,
          name: 'getRedeemableBonds',
          params: [],
        },
      ]

      const [getBurnableDollarLeft, getRedeemableBonds] = await multicall((treasury as unknown) as any, calls)
      setData([new BigNumber(getBurnableDollarLeft).div(oneUnit), new BigNumber(getRedeemableBonds).div(oneUnit)])
    }

    if (contract) fetchAllocations()
  }, [contract])
  return data
}

export default useExitBoardroom
